<template>
  <div class="container">
    <div class="toptip">
      <span>欢迎注册</span>
    </div>
    <div>
      <van-cell-group>
        <van-field
          v-model.trim="regModel.tel"
          :error="usertel"
          type="number"
          maxlength="11"
          center
          clearable
          placeholder="请输入手机号"
          label="手机号"
        />
        <van-field
          v-model.trim="regModel.password"
          type="password"
          maxlength="6"
          center
          clearable
          placeholder="请输入新密码"
          label="新密码"
        />
        <van-field
          v-model.trim="regModel.rpassword"
          type="password"
          maxlength="6"
          center
          clearable
          placeholder="请再次输入新密码"
          label="新密码"
        />
        <van-field
          v-model.trim="regModel.question"
          type="text"
          maxlength="12"
          center
          clearable
          label="密保问题"
          placeholder="请输入问题(方便找回登录密码)"
        />
        <van-field
          v-model.trim="regModel.answer"
          maxlength="6"
          center
          clearable
          label="密保答案"
          placeholder="请输入答案"
        />
      </van-cell-group>
      <div style="margin: 16px;">
        <van-button round block type="info" :loading="loading" loading-text="注册中..." @click="reg">注册</van-button>
      </div>
      <div class="pwdreg">
        <span @click="goback">返回登录界面</span>
      </div>
    </div>
  </div>
</template>

<script>
import { validateTel } from "@/utils/validator";
import { Toast } from "vant";
import { userReg } from "@/api/user";
export default {
  name: "register",
  data() {
    return {
      usertel: false,
      regModel: {
        tel: "",
        password: "",
        rpassword: "",
        question: "",
        answer: ""
      },
      loading: false
    };
  },
  watch: {
    tel(value) {
      if (value.length === 11) {
        if (validateTel(value)) {
          this.usertel = false;
        } else {
          this.usertel = true;
          Toast("手机号格式错误");
        }
      } else {
        this.usertel = false;
      }
    }
  },
  methods: {
    sendSms() {
      
    },
    goback() {
      this.$router.push("/login");
    },
    async reg() {
      if (!this.regModel.tel) {
        Toast("手机号不能为空");
        return;
      }
      if (!validateTel(this.regModel.tel)) {
        Toast("手机号格式有误");
        return;
      }
      if (!this.regModel.password) {
        Toast("新密码不能为空");
        return;
      }
      if (this.regModel.password !== this.regModel.rpassword) {
        Toast("两次密码不一致");
        return;
      }
      if (!this.regModel.question) {
        Toast("密保问题不能为空");
        return;
      }
      if (!this.regModel.answer) {
        Toast("密保答案不能为空");
        return;
      }
      this.loading = true;
      let res = await userReg(this.regModel);
      if (res) {
        if (res.type === 1) {
          //未注册
          setTimeout(() => {
            this.loading = false;
            this.$router.push("/login");
          }, 2000);
        } else if (res.type === 2) {
          //已注册
          this.loading = false;
        } else if (res.type === 3) {
          //注册出错
          this.loading = false;
        }
        Toast(res.message);
      } else {
        Toast("似乎出现了错误");
        this.loading = false;
      }
      this.regModel.tel = ""
      this.regModel.password = ""
      this.regModel.rpassword = ""
    }
  }
};
</script>
<style lang="scss" scoped>
.pwdreg {
  position: relative;
  margin-top: 20px;
  text-align: center;
  span {
    margin: 0 5px 0 5px;
  }
}
.toptip {
  position: relative;
  width: 100%;
  height: 100px;
  padding: 10px 0 20px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  span {
    width: 100%;
    text-align: center;
    font-size: 32px;
  }
}
</style>